import React, {Component} from 'react';
import {Badge, Button, Card, Col, Container, Row} from "react-bootstrap";
import '../media-req.css'
import {RouteComponentProps} from "react-router";
import {ProblemCardDisplay} from "../Components/ProblemCardDisplay";
import {problems_list, tickets_list} from "../StaticData/Tickets";
import TicketTesting from "../Components/TicketTesting";
import { Link } from "react-router-dom";

interface MatchParams {
}

export interface HomePageProps extends RouteComponentProps<MatchParams>{
}

export type HomePageState = {}

export class HomePage extends Component<HomePageProps, HomePageState> {
    constructor(props: HomePageProps) {
        super(props);
        this.state = {};
    }

    componentDidMount(): void {
    }

    render() {
        return (
            <Container fluid='sm'>
                <Row>
                    <Col>
                        <Card>
                            <Card.Header>Список билетов</Card.Header>
                            <Card.Body>
                                <Container>
                                    {tickets_list.map((ticket) => (
                                        <Row>
                                            <Col>
                                                <Card>
                                                    <Card.Header as='h5'>
                                                        Билет №{ticket.id}
                                                    </Card.Header>
                                                    <Card.Body>
                                                        <Container fluid>
                                                            <Row>
                                                                <Col>
                                                                    <Button href={process.env.PUBLIC_URL + '/#/ticket/' + ticket.id} variant='info'>Просмотр билета</Button>
                                                                </Col>
                                                                <Col>

                                                                    <Button href={process.env.PUBLIC_URL + '/#/testing/' + ticket.id} variant='primary'>Тестирование</Button>
                                                                </Col>
                                                                {/*<Col>*/}

                                                                {/*    <Button href={process.env.PUBLIC_URL + '/#/checking/' + ticket.id} variant='success'>#def проверка</Button>*/}
                                                                {/*</Col>*/}
                                                            </Row>
                                                        </Container>
                                                    </Card.Body>
                                                </Card>
                                            </Col>
                                        </Row>
                                    ))}
                                </Container>
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
            </Container>

        );
    }
}